import text from './text'
let hack = document.getElementById("hack");
let style = document.getElementById("style");
let html = document.querySelector("html");

let length = text.length;
let i = 0;
//flagç”¨æ¥æ ‡å¿—styleçš„å†…å®¹æ˜¯å¦æ¥å—ä¿®æ”¹
let flag = false;
let turn = true;
let text2 = "";
let speed = 30
let timer = undefined
let step = () => {
    if (timer) {
        window.clearTimeout(timer)
    }
    console.log(speed);
    //æ¯æ¬¡æ‰§è¡Œstepéƒ½å£°æ˜äº†ä¸€ä¸ªå®šæ—¶å™¨ï¼Œé‚£éœ€è¦æ¸…é™¤ä¸€ä¸ªå®ƒï¼Œå†å¼€å§‹ä¸€ä¸ªæ–°çš„
    if (turn) {
        timer = setTimeout(function () {
            if (i < length) {
                text2 += text[i];
                text[i - 1] === "@" ? flag = true : null;
                flag ? style.innerHTML += text[i] : null;
                text[i + 1] === "&" ? flag = false : null;
                hack.innerText = text2;
                i += 1;
                window.scrollTo({
                    top: 9999,
                    left: 0,
                    behavior: 'smooth'
                });
                step(speed);
            }
        }, speed)
    }
}
step(speed);

$(".off").on('click', () => {
    turn = false
});
$(".on").on('click', () => {
    turn = true
    //å¦‚æœæˆ‘ç‚¹å¾ˆå¤šæ¬¡onçš„è¯ï¼Œä¼šç”Ÿæˆå¤šä¸ªstepï¼Œæ€ä¹ˆæ¸…é™¤ä¹‹å‰çš„å‘¢
    step(speed)
});
$(".fast").on('click', () => {
    speed = 30
});
$(".normal").on('click', () => {
    speed = 300
});
$(".slow").on('click', () => {
    speed = 600
});

